.app__works {
  flex: 1;
  width: 100%;
  flex-direction: column;
}
.main-title {
  font-size: 180px; 
  padding: 1rem 4rem;
  color: white;
  flex: 1;
  width: 100%;
  flex-direction: row;

  @media screen and (max-width: 450px){
    padding-top: 100px;
    font-size: 40px;
    display: flex;
    justify-content: center;
}
@media screen and (max-width: 1200px){
    padding-top: 100px;
    font-size: 80px;
    display: flex;
    justify-content: center;
}

}

.app__work-filter {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;
  flex-wrap: wrap;
  margin: 4rem 0 2rem;

  .app__work-filter-item {
    padding: 0.5rem 1rem;
    border-radius: 2rem;
    background-color: #fff;
    color: #000;
    font-weight: 800;

    cursor: pointer;
    transition: all 0.3s ease;
    margin: 0.5rem;

    &:hover {
      background-color: var(--secondary-color);
      color: #fff;
    }

    @media screen and (min-width: 2000px) {
      padding: 1rem 2rem;
    }
  }

  .item-active {
    background-color: var(--secondary-color);
    color: #fff;
  }
}

.app__work-portfolio {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;

  .app__work-item {
    width: 500px;
    flex-direction: column;
    margin: 2rem;
    color: #000;

    cursor: pointer;
    transition: all 0.3s ease;

    &:hover {
      box-shadow: 0 0 25px rgba(0, 0, 0, 0.2);
    }

    @media screen and (min-width: 2000px) {
      width: 470px;
      padding: 1.25rem;
    }

    @media screen and (max-width: 1200px) {
      width: 450px;
      padding: 1rem;
    }

    @media screen and (max-width: 450px) {
      width: 90vw;
      padding: 0rem;
    }

  }
}

.app__work-img {
  width: 100%;
  height: 300px;
  

  position: relative;

  img {
    width: 100%;
    height: 100%;
    border-radius: 2rem;
    object-fit: cover;
  }

  @media screen and (min-width: 2000px) {
    height: 350px;
  }
}

.app__work-hover {
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  width: 100%;
  height: 100%;
  background-color: rgba($color: #111010, $alpha: 0.5);

  border-radius: 0rem;
  opacity: 0;
  transition: all 0.3s ease;

  div {
    width: 50px;
    height: 50px;
    border-radius: 0%;
    background-color: rgba(0, 0, 0, 0.5);
    color: #fff;

    margin: 1rem;
    font-family: var(--font-base);
    font-weight: 800;
    cursor: pointer;
    transition: all 0.3s ease;

    svg {
      width: 50%;
      height: 50%;
      color: var(--white-color);
    }
  }
}

.app__work-content {
  padding: 0.5rem;
  width: 100%;
  position: relative;
  flex-direction: column;
  display: flex;
  color: white;


  h4 {
    margin-top: 1rem;
    line-height: 1.5;

    @media screen and (min-width: 2000px) {
      margin-top: 3rem;
    }
  }

  .app__work-tag {

    padding: 0.5rem 1rem;
    background-color: #fff;
  }
}